export default {
	props:{
		dataList:{
			type:Array,
			default:[]
		}
	},
	methods:{
		onClick(v){
			this.$api.navTo(`/pages/messageDetails?id=${v.id}`)
		},
		delItem(v,k){
			console.log('长按')
			let self=this;
			uni.showModal({
				cancelText:'取消',
				confirmText:'确定',
				title:'确定删除吗?',
				success(res) {
					if(res.confirm){
						let parmas={
							id:v.id
						}
						self._reqw.ipost(parmas,'delMessage').then(res=>{
							res.result==0?(self.dataList.splice(k,1),self.$api.tip('删除成功')):self.$api.tip(res.resultNote)
						}).catch(err=>{})
					}
				}
			})
			
		}
	}
}
